    import { mapState } from 'vuex'
    export default {
        data() {
            return {
                showBox:false,
                aboutInfo: '',
                isBusiness: false
            }
        },
        onShow() {
            this.checkBusiness()
        },
        computed: {
            ...mapState(['userInfo'])
        },
        methods: {
            intoOrder () {
                uni.navigateTo({
                    url: '/pages/orderList/orderList',
                });
            },
            intoMyCard () {                		
				uni.switchTab({
					url: '/pages/myCard/myCard'
				})
            },
            intoRecodeList () {                		
				uni.navigateTo({
					url: '/pages/recodeList/recodeList'
				})
            },
            async getAbout () {
                if (!this.userInfo) return
                let res = await this.$api.getAboutList({type:'about'})
                if (res.code === '0') {
                    let ele = await this.$api.getAbout(res.data[0].id)
                    if (ele.code === '0') {
                        this.aboutInfo = ele.data.content
                        this.version = ele.data.version
                        let temp = uni.getStorageSync('about-version')
                        if (temp !== this.version) {
                            uni.setStorageSync('about-version',this.version)                            
                            this.showBox = true
                        }
                    }
                }
                
            },
            async checkBusiness () {                
                let res = await this.$api.checkBusiness().then((res) => {
                    if (res.code === 0) {
                        if (res.data.length > 0) {
                            this.isBusiness = true
                        }
                    }
                })
            },
            showAbout () {
                this.getAbout()
                this.showBox = true
            },
            closeBox () {
                this.showBox = false
            }
        }
    }
